.popular-categories {
    margin-bottom: 43px;
    margin-top: 60px;
    @media all and (max-width:900px) {
        margin-top: 35px;
    }
    &__item {
        border-radius: 9px;
        border: 1px solid #FFF;
        background: radial-gradient(56.02% 50.16% at 80.75% 37.15%, #F1F1F1 0%, #F5F5F5 100%);
        display: flex !important;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        padding: 53px 0 25px 0;
        grid-gap: 40px;
        text-decoration: none;
        color: #000;
        box-sizing: border-box;
        @media all and (max-width:600px) {
            padding: 35px 0 15px 0;
            grid-gap: 20px;
            font-size: 12px;
        }
        &:hover {
            border: 1px solid #F0820F;
            background: #FFF;
        }

    }
    &__image img {
        width: 100%;
        object-fit: contain;
        @media all and (max-width:600px) {
            max-width: 75px;
        }
    }
    &__title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 30px;
    }
    &__button-all {
        display: block;
        padding-right: 23px;
        position: relative;
        text-decoration: none;
        color: inherit;
        // @media all and (max-width:900px) {
        //     display: none;
        // }
        &::after {
            content: "";
            position: absolute;
            top: 3px;
            right: 0px;
            width: 7px;
            height: 15px;
            background: url("../img/arrow-grey.svg") 0 0/contain no-repeat;
        }
    }
    &__arrows div {
        width: 36px;
        height: 72px;
        border-radius: 5px;
        background: #fff;
        border: none;
        outline: none;
        @media all and (max-width:600px) {
            display: none;
        }
    }
    &__arrows div:hover {
        background: #FEF3E7; 
    }
    &__slider {
        position: relative;
    }
    &__arrows {
        .swiper-button-prev {
            position: absolute;
            left: -18px;
            top: 50%;
            transform: translate(0, -50%);
            cursor: pointer;
            img {
                transform: rotate(180deg);
            }
            &::after {
                content: none;
            }
        }
        .swiper-button-next {
            position: absolute;
            right: -18px;
            top: 50%;
            transform: translate(0, -50%);
            cursor: pointer;
            &::after {
                content: none;
            }
        }
    }
    &__scrollbar {
        margin-top: 40px;
        position: relative!important;
        @media all and (max-width:600px) {
            margin-top: 20px;
        }
    }
    &__slider-container {
        position: relative;
    }
}